<template>
	<div class="container-scrollable">
		<transition-group name="screen-list" class="screen-list" tag="ul">
			<li class="screen-list__screen"
			    v-for="(screen, index) of SCREEN_LIST"
			    :key="screen.key"
			    v-if="screen.visible"
			>
				<screen-item
					:screenData="screen"
					:index="index"
					:key="index"
					v-if="screen.visible"
				></screen-item>
				<transition name="to-left">
					<div class="msg-box msg-answer"
					     v-if="screen.answer"
					>
						<span v-html="screen.answer"></span>
					</div>
				</transition>
			</li>
		</transition-group>
	</div>
</template>

<script>
  import ScreenItem from './ScreenItem'
  import {mapGetters} from 'vuex'

  export default {
    name: 'ScreenList',
    components: {
      ScreenItem
    },
    mounted() {
    },
    computed: {
      ...mapGetters(['SCREEN_LIST']),
    },
  }
</script>

<style lang="scss">
	.container-scrollable {
		display: flex;
		flex-direction: column;
		justify-content: stretch;
		overflow-x: hidden;
		overflow-y: scroll;
		height: calc(100vh - 62px);
		&::-webkit-scrollbar {
			background: #f0f0f0;
			width: 15px;
			box-shadow: inset 0 0 5px 5px rgba(#000, .5);
		}

		&::-webkit-scrollbar-track {
			/*box-shadow: inset 0 0 4px 3px rgba(#f0f0f0, .8);*/
			background-color: #f0f0f0;
			box-shadow: 0 0 1px 1px #eee,
			inset 0 0 7px 2px rgba(0,0,0,.5)
		}
		&::-webkit-scrollbar-thumb {

			/*box-shadow: inset 0 0 8px 3px rgba(#000, .5),
			inset 0 -2px 4px rgba(#000, .8),
			0 -3px 5px 2px rgba(#000, .5),
			0 14px 5px 2px rgba(#222, .4);*/
			border-radius: 20px;
			width: 10px;
			background: linear-gradient(left, rgba(#000, .2), rgba(#000, .6));
			/*background-color: rgba(#ff4c4c, .8);*/
			box-shadow: inset 0 0 1px 1px #5C6670;
		}
	}

	.screen-list {
		padding: 0 20px 30px;
		&__screen {
			margin-bottom: 35px;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	.msg-answer {
		background: #cfe2fa;
		margin: 20px 0 20px auto;
		min-height: 50px;
		span {
			position: relative;
			z-index: 10;
		}
	}

</style>