<template>
	<transition name="screen-appear">
		<div v-if="screen.visible">
			<div class="screen__bg" v-if="img">
				<img :src="imgSrc" alt="">
			</div>
			<div v-else-if="screen.video"
			     v-html="screen.video"
			>
			</div>
			<transition name="to-right">
				<div class="msg-box msg-question"
				     :class="{'to-right': screen.visible}"
				     v-if="screen.msgOut"
				>
					<div class="msg-inner" v-html="screen.msgOut"></div>
				</div>
			</transition>
			<div class="btn-group">
				<v-button v-for="button of screen.buttons"
				          :class="button.class"
				          @click="sendMessage([index, screen.id, button.id, button.text])"
				          :disabled="button.clicked === true"
				          v-html="button.text"
				>
				</v-button>
			</div>
		</div>
	</transition>
</template>

<script>
  import VButton from '../VButton'
  import {mapActions} from 'vuex'

  export default {
    components: {
      VButton,
    },
    props: {
      screenData: Object,
	    index: Number,
    },
    data() {
      return {
        screen: {},
        img: null,
      }
    },
    mounted() {
      this.screen = this.screenData;
      this.img = this.screen.img;
    },
    computed: {
      imgSrc() {
        if (!this.img) {
          return
        }
        return require(`../../assets/img/${this.img}`)
      }
    },
    methods: {
      ...mapActions(['getAnswer']),
	    sendMessage(data) {
				this.getAnswer(data);
      },
    },

  }
</script>

<style lang="scss">
	.screen__bg {
		/*min-height: 400px;*/
	}
	.btn-group {
		margin: 20px 0;
	}
	.btn-sex {
		width: 100%;
		text-transform: uppercase;
		font-style: italic;
		font-weight: 800;
		font-size: 16px;
		&--female {
			background: #ff4c4c;
		}
		&--male {
			background: #246376;
		}
	}
	.msg-box {
		position: relative;
		width: 45%;
		border-radius: 7px;
		padding: 10px 5px 10px 15px;
		line-height: 1.5;
		transition: .3s;
		&:after {
			content: '';
			position: absolute;
			top: 0;
			width: 54px;
			border: 13px solid transparent;
		}
		&.msg-question:after {
			border-top-color: #f0f0f0;
			left: -13px
		}
		&.msg-answer:after {
			right: -13px;
			border-top-color: #cfe2fa;
		}
	}
	.msg-question {
		transform: translateX(-50%);
		opacity: 0;
		background: #f0f0f0;
		margin-top: 15px;
	}
	.msg-inner {
		padding: 7px 20px 7px 0;
		p {
			margin-bottom: 20px;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}

</style>