<template>
	<div class="msg-input"
	     :class="{'disabled': !LOCK}"
	     @keypress="sendValue(value, $event)"
	>
		<div class="msg-icon-smile">
			<img src="../../static/img/smile.png" alt="">
		</div>
		<div class="msg-input__field">
			<input
			       type="text"
			       v-model="value"
			       :disabled="LOCK"
			       :placeholder="PLACE_HOLDER"
			       ref="input"
			>
		</div>
		<button class="msg-input__btn"
		        @click="sendValue(value, $event)"
		        :disabled="LOCK"
		>
			<svg id="Layer_1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24"><path fill="#3b85f5" fill-opacity="1" d="M1.101 21.757L23.8 12.028 1.101 2.3l.011 7.912 13.623 1.816-13.623 1.817-.011 7.912z"></path></svg>
		</button>
	</div>
</template>

<script>
	import {mapGetters,mapActions} from 'vuex'
  export default {
    data() {
      return {
        value: ''
      }
    },
	  watch: {
      LOCK() {
        if (!this.LOCK) {
          let inp = document.querySelector('.msg-input input');
          setTimeout(() => inp.focus(), 200);
        }
      }
	  },
	  computed: {
		  ...mapGetters(['LOCK','SCREEN_INDEX','PLACE_HOLDER'])
	  },
	  methods: {
		  ...mapActions(['setName']),
		  sendValue(val, event) {
		    if (event.type === 'keypress') {
          if (event.keyCode === 13 || event.keyCode === 32) {
            if (!val) {
              alert('Введите имя');
              return
            }
            this.setName([val, this.SCREEN_INDEX]);
            this.value = '';

            // setTimeout(() => this.scroll(), 50);
          } else return;

		    } else if (event.type === 'click') {
          if (!val) {
            alert('Введите имя');
            return
          }
          this.setName([val, this.SCREEN_INDEX]);
          this.value = '';

          // setTimeout(() => this.scroll(), 50);
		    }

		  },
      scroll() {
        let container = document.querySelector('.container-scrollable');
        let list = document.querySelector('.screen-list');
        container.scrollTop = list.scrollHeight;
      }
	  },
	  beforeDestroy() {
      // window.removeEventListener('keypress', this.sendValue);
	  }
  }
</script>

<style lang="scss">
	.msg-input {
		position: relative;
		z-index: 100;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 10px 30px 10px 20px;
		background: #f0f0f0;
		&__field {
			width: 100%;
			input {
				padding: 6px 10px 6px 20px;
				height: 42px;
				width: 95%;
				border-radius: 21px;
				background: #fff;
				border: 1px solid transparent;
				transition: .3s;
				font-size: 16px;
				box-shadow: 0 0 2px 0 rgba(0,0,0,.5);
				&:focus {
					/*border: 2px solid #246376;*/
					box-shadow: 0 0 2px 0 rgba(0,0,0,.5),
						inset 0 0 4px 1px rgba(0,0,0,.5);

				}
				&:disabled {
					cursor: not-allowed;
					box-shadow: none;
				}
				&::placeholder {
					color: #999;
					font-size: 16px;
				}
			}
		}
		&__btn {
			padding: 5px 10px;
			background: none;
			border: none;
		}
	}
	.msg-icon-smile {
		width: 30px;
		margin-right: 10px;
		cursor: pointer;
	}
</style>