  mutations: {
    SET_INDEX(state, index) {
      state.index = index;
    },
    CHANGE_PLACEHOLDER(state, index) {
      if (index === 1) {
        state.placeholder = 'Введите ваше имя'
      } else if (index === 17) {
        state.placeholder = 'Введите кодовое слово'
      }
    },
    GET_ANSWER(state, data) {
      let [index, id, answer] = data;
      if (state.screenList[index].id === id) {
        state.screenList[index].answer = answer;
      }
    },
    SHOW_NEXT_SCREEN(state, nextIndex) {
      if (!state.screenList[nextIndex]) return;
      state.index = nextIndex || state.index + 1;
      state.nextScreenIndex = state.index;
      state.screenList[state.nextScreenIndex].visible = true;
      state.nextScreenIndex++;
    },
    SHOW_LOADER(state, boolean) {
      state.loading = boolean;
    },
    UNLOCK_INPUT(state) {
      if (state.screenList[state.index].visible) {
        state.lockInput = false;
      }
    },
    SET_NAME(state, params) {
      let [val, index] = params;
      state.index = index;
      state.name = val;
      state.screenList[index].answer = val;
      state.lockInput = true;
    },
    WHAT_YOU_WANT(state, data) {
      let [index, btnId] = data;
      let screen = state.screenList[index];

      screen.choices.forEach(choice => {
        if (choice.id === btnId) {
          screen.img = choice.img;
          screen.msgOut = choice.text;
        }
        if (btnId === 3) {
          screen.buttons[0].text = "Ок)"
        }
      });
    }
  },
  actions: {
    async getAnswer({commit, state}, data) {
      let container = document.querySelector('.container-scrollable'),
        list = document.querySelector('.screen-list');

      let [index, id, btnId, answer] = data;
      commit('SET_INDEX', index);

      let promise = new Promise(resolve => {
        commit('SHOW_LOADER', true);
        setTimeout(() => {
          commit('SHOW_LOADER', false);
          commit('GET_ANSWER', [index, id, answer]);
          setTimeout(() => {
            container.scrollTop = list.scrollHeight;
          }, 100);
          resolve(true);
        }, 1000)
      })
        .then(response => {
          commit('SHOW_LOADER', response);
          setTimeout(() => {
            if (id === 3) {
              commit('GET_ANSWER', [index, id, answer]);
              setTimeout(() => {
                container.scrollTop = list.scrollHeight;
              }, 100);
              commit('WHAT_YOU_WANT', [id, btnId]);
            }
            commit('SHOW_NEXT_SCREEN', id);
          }, 1300);
          return false
        })
        .then(response => {
          setTimeout(() => {
            commit('SHOW_LOADER', response);
            container.scrollTop = list.scrollHeight;
          }, 1500)
        });

      // setTimeout(() => ,300);

      if (index === 1 || index === 17) {
        commit('CHANGE_PLACEHOLDER', index);
      }
      if (state.screenList[index].id === id) {
        if (id === 1 || id === 17) {
          commit('UNLOCK_INPUT');
        }

        // setTimeout(() => commit('SHOW_NEXT_SCREEN', id), 500);
      }
    },
    setName({commit, state}, params) {
      let [name, index] = params;
      let container = document.querySelector('.container-scrollable'),
        list = document.querySelector('.screen-list');

      if (index === 1) {
        commit('SET_NAME', params);
      }
      commit('SHOW_LOADER', true);
      setTimeout(() => {
        commit('SHOW_NEXT_SCREEN', index + 1)
        commit('SHOW_LOADER', false)
      }, 800);
      setTimeout(() => container.scrollTop = list.scrollHeight, 1000);
    },
    sendMsg({commit, state}, msg) {
      if (state.screenList)
        commit('SEND_MSG', msg);
    }
  },
  getters: {
    SCREEN_LIST: state => state.screenList,
    PLACE_HOLDER: state => state.placeholder,
    LOCK: state => state.lockInput,
    USER_NAME: state => state.name,
    SCREEN_INDEX: state => state.index,
    LOADING_ON: state => state.loading,
  }
